.accordion {
    margin: 0 auto;
    width: 100%;

    // .accordion__control
    max-width: 600px;

    &:not(:last-child) {
        margin-bottom: 20px;
    }

    &__control {
        display: flex;
        justify-content: space-between;
        padding: 30px;
        width: 100%;
        list-style: none;
        border: 1px solid #000;
        background-color: transparent;
        cursor: pointer;
    }

    // .accordion__title

    &__title {
        font-family: sans-serif;
        font-weight: 700;
        font-size: 18px;
    }

    // .accordion__icon

    &__icon {
        position: relative;
        width: 30px;
        height: 30px;
        border-radius: 100%;
        background-color: #ccc;

        &::before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            width: 10px;
            height: 2px;
            background-color: #000;
            transform: translate(-50%, -50%);
        }

        &::after {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            width: 2px;
            height: 10px;
            background-color: #000;
            transform: translate(-50%, -50%);
        }
    }

    // .accordion__content

    &__content {
        box-sizing: content-box;
        opacity: 0;
        padding: 0 20px;
        max-height: 0;
        overflow: hidden;
        border: 1px solid #000;
        border-top: transparent;
        transition: all 0.3s ease-out;
        will-change: max-height;
    }
}

.accordion.open .accordion__content {
    opacity: 1;
    padding: 20px;
}

.accordion__control::-webkit-details-marker {
    display: none;
}

.accordion.open .accordion__icon {
    &::after {
        display: none;
    }
}